.SdiApplicationView {
  height: 100%;
  &.wait,
  &.wait * {
    cursor: wait !important;
  }
}

.SdiApplicationView__body {
  margin: 0;
  height: 100%;
  > div {
    height: 100%;
  }
}

.SdiApplicationView__container {
  height: 100%;
  font-family: sans-serif;
  display: grid;
  grid-template-rows: max-content auto max-content;
}

.SdiApplicationView__main {
  background-color: gray;
}
